<template>
  <div class="hand-prize-box">
    <div class="hpb-container">
      <div class="hpb-background">
        <div class="hpb-content">
          <div class="hpb-list" v-for="i in 3" :key="i">
            <ul :ref="'hpbList'+ i">
              <div class="hpb-the-why"></div>
              <li v-for="(item, index) in array" :key="index">
                <div>
                  <div class="hpb-avatar">
                    <img :src="item.avatar" alt="">
                  </div>
                  <div class="hpb-person">
                    <div class="hpb-name">{{item.name}}</div>
                    <div class="hpb-txt">{{item.txt}}</div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="hpb-footer">
          <button class="hpb-btn" @click="start()">开始抽奖</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'handPrizeBox',
  data () {
    return {
      array: [{
        name: '陈总',
        txt: '我就是一个老炮儿',
        avatar: 'https://gss2.bdstatic.com/9fo3dSag_xI4khGkpoWK1HF6hhy/baike/awhcrop%3D200%2C200/sign=9707448c48166d222e3d48c7344c79ca/3ac79f3df8dcd100785d3b66798b4710b8122fb0.jpg'
      }]
    }
  },
  methods: {
    start () {
      this.startSlide(this.$refs.hpbList1[0])
      window.setTimeout(() => {
        this.startSlide(this.$refs.hpbList2[0])
      }, 400)
      window.setTimeout(() => {
        this.startSlide(this.$refs.hpbList3[0])
      }, 800)
    },
    startSlide (ulEle) {
      if (!ulEle || !ulEle.nodeName) {
        return
      }

      let val = 0           // translate的值，往负了走
      let interval = 0.5    // 开始的叠加值，最大为4
      let slow = false      // 减速带
      let timer = null
      let connectionDiv = ulEle.cloneNode(true)
      connectionDiv.removeChild(connectionDiv.children[0])
      connectionDiv = connectionDiv.innerHTML

      timer = window.setInterval(() => {
        // 开始 translate
        val -= interval

        // 当超过设置阈值时，开始减速
        if (!slow) {
          interval = interval + 0.008 >= 4 ? 4 : interval + 0.008
        } else {
          interval = interval - 0.008 <= 0.5 ? 0.5 : interval - 0.008
        }

        let innerHeight = parseInt(ulEle.offsetHeight)
        // 380 为抽奖箱的高度约值，当超过这个高度时，需要进行填补
        if (Math.abs(val) + 380 > innerHeight) {
          //
          ulEle.innerHTML += connectionDiv
          //
          const blockDiv = ulEle.children[0]
          let i = Math.floor((Math.abs(val) - blockDiv.clientHeight) / 58)
          // 去掉一些多余的 li 必要页面卡顿
          // 同时往 blockDiv 填充高度
          while (i-- > 0) {
            ulEle.removeChild(ulEle.children[1])
            blockDiv.style.height = (blockDiv.clientHeight + 58) + 'px'
          }
        }

        if (Math.abs(val) >= 10000) {             // 当 translateY 大于 10000开始减速
          slow = true
        }

        if (slow && interval <= 0.5 && Math.abs(val) >= 11020) {
          window.clearInterval(timer)
        }

        ulEle.style.transform = `translateY(${val}px)`
      }, 1)
    }
  },
  components: {
    // HelloWorld
  },
  mounted () {
    console.log(window.m = this)
    let i = 0
    while (i++ < 9) {
      this.array.push(this.array[0])
    }
  }
}
</script>

<style lang="scss">
  .hand-prize-box {
    .hpb-container {
      .hpb-background {
        position: relative;
        width: 595px;
        height: 671px;
        background-image: url(../assets/images/center.png);
        background-size: 100% 100%;
        margin: 0 auto;
      }
      .hpb-content {
        position: absolute;
        top: 151px;
        left: 53px;
        right: 86px;
        bottom: 146px;

        display: flex;
        padding: 0 8px 0 0px;
        background-color: red;
        .hpb-list {
          flex: 1;
          overflow: hidden;
          ul {
            li {
              height: 50px;
              margin-top: 8px;
              padding: 0 0 0 8px;
              > div {
                background-color: #fff;
                height: 100%;
                border-radius: 3px;
                display: flex;
                padding: 4px 0 4px 6px;
              }
              .hpb-avatar {
                border: 2px solid #fff;
                border-radius: 50%;
                width: 40px;
                height: 40px;
                overflow: hidden;
                img {
                  width: 100%;
                  height: 100%;
                }
              }
              .hpb-person {
                flex: 1;
                font-size: 14px;
                text-align: left;
                overflow: hidden;
                padding: 4px 0 0 4px;
              }
              .hpb-name {
                font-weight: bold;
                margin-bottom: 2px;
              }
              .hpb-txt {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 12px;
              }
            }
          }
        }
      }
      .hpb-footer {
        position: absolute;
        bottom: 40px;
        width: 100%;
        padding: 0 0 0 20px;
        .hpb-btn {
          transition: all .25s;
          font-size: 26px;
          color: #fff;
          padding: 0 0 14px 0;
          letter-spacing: 3px;
          width: 186px;
          height: 69px;
          background-image: url(../assets/images/start_btn.png);
          margin: 0 0 0 -50px;
          &:active {
            background-position: 0px 8px;
            padding: 0 0 6px 0;
          }
        }
      }
    }
  }
</style>
