<script>

export default {
  name: 'threeDBox',
  data () {
    return {
      move: false,
      move1: false,
      down: false,
      up: false,
      rotate: false,

      move2: false,
      working: false,
      array: []
    }
  },
  methods: {
    start () {
      if (this.move) { // 抽奖中，不支持响应
        return false
      }
      if (this.working === false) {
        this.working = true
      } else {
        this.working = false
        this.showHand()
      }
    },

    showHand () {
      this.move = true
      this.$refs.theHand.style.opacity = 1

      window.setTimeout(() => {
        this.down = true
      }, 500)

      window.setTimeout(() => {
        this.up = true
      }, 500 + 1200)

      window.setTimeout(() => {
        this.rotate = true
        this.move1 = true
      }, 500 + 1200 + 1200)

      window.setTimeout(() => {
        this.$refs.theHand.style.opacity = 0
      }, 500 + 1200 + 1200 + 1000)

      window.setTimeout(() => {
        this.move = false
      }, 500 + 1200 + 1200 + 1000 + 1000)

      window.setTimeout(() => {
        this.up = false
        this.down = false
        this.rotate = false
        this.move1 = false
      }, 500 + 1200 + 1000 + 1000 + 1000 + 1000)
    }

  },
  components: {},
  mounted () {
    console.log(window.m = this)
    window.setTimeout(() => {
    }, 1000)
  }
}
